@import url(https://fonts.googleapis.com/css2?family=Secular+One&display=swap);
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body{
  font-family: 'Secular One', sans-serif;
  position: relative;
}
.Game{
  margin-top: 20vh;
  min-height: 80vh;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.title{
  font-size: 3rem;
  margin-bottom: 20px;
}
.square{
    height: 48px;
    width: 48px;
    border: 1px solid black;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2.5rem;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
}

.row{
    display: flex;
}
.status{
    position: relative;
    font-size: 1.25em;
}
.board{
    position: relative;
}
.control{
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
}
.control button{
    min-height: 25px;
    text-transform: capitalize;
    font-size: large;
    padding: 5px;
    cursor: pointer;
    border: 1px solid black;
}
.control button:focus{
    outline: none;
}
.status{
    text-align: center;
}

.game {
  display: flex;
  justify-content: center;
}
.game-info {
  width: 100%;
  margin-left: 40px;
  text-transform: capitalize;
}
.game-board {
  margin-right: 40px;
}
.info {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.info-o,
.info-x {
  margin: 0px 5px;
  min-width: 150px;
  display: flex;
  flex-direction: column;
}
.info-x .status {
  color: red;
}
.info-o .status {
  color: blue;
}
.move {
  display: flex;
  flex-direction: column;
}
.title-1{
  font-size: 2rem;
  min-width: 320px;
  text-align: center;
}
button {
  margin: 5px;
  width: 100%;
  display: inline-block;
  padding: 0.35em 1.2em;
  border: 0.1em solid #000000;
  margin: 0 0.3em 0.3em 0;
  border-radius: 0.12em;
  font-weight: 300;
  background-color: #ffffff;
  color: #000000;
  text-align: center;
  transition: all 0.3s;
  font-family: 'Secular One', sans-serif;
}
button:hover {
  color: #ffffff;
  background-color: #000000;
}
button:active{
  outline: none;
}
img{
  max-width: 20px;
  height: auto;
}
.current{
  color: #000000;
  background-color: #ffd571;
}
